
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
name: 'SecuritySetting',
directives: {
TransferDom
},
components: {
XInput,
ViewBox,
XButton,
XHeader,
Box,
Divider,
Group,
Cell,
CellBox,
PopupHeader,
Popup
},
mounted: function () {
this.currentUser = JSON.parse(window.localStorage.getItem('User'))
},
data () {
return {
msg: 'resetSetting page',
showEdit: false,
oldPassword: '',
newPassword: '',
repeatPassword: '',
phoneNumber: '',
newPayPassword: '',
smsCode: '',
currentUser: {
nickName: ''
},
editModel: {
title: '',
currentEdit: 'nickName'
}
}
},
methods: {
logout () {
window.localStorage.clear()
this.$router.push({'name': 'login'})
},
resetPasswd () {
this.$router.push({name: 'resetPasswd'})
},
resetLoginPasswd () {
this.editModel.title = ''
this.editModel.currentEdit = 'loginPassword'
this.showEdit = true
},
resetPayPasswd () {
this.editModel.title = this.currentUser.passwordReset ? '' : ''
this.editModel.currentEdit = 'payPassword'
this.showEdit = true
},
doReset () {
if (this.editModel.currentEdit === 'loginPassword') {
if (this.oldPassword.length <= 0 || this.newPassword.length <= 0 || this.repeatPassword.length <= 0) {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
} else {
if (this.$refs.input1.valid && this.$refs.input2.valid && this.$refs.input3.valid) {
if (this.newPassword === this.repeatPassword) {
let requestOptions = {
url: this.AppConfig.apiServer + '/user/updateLoginPassword',
params: {
account: this.currentUser.account,
oldPassword: this.oldPassword,
newPassword: this.repeatPassword
}
}
this.doRequest(requestOptions)
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
}
}
}
if (this.editModel.currentEdit === 'payPassword') {
this.showEdit = false
}
},
doRequest (requestOptions) {
this.doPost(requestOptions).then(result => {
if (result.success) {
this.oldPassword = ''
this.newPassword = ''
this.repeatPassword = ''
this.showEdit = false
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'text',
text: result.message,
width: '10em'
})
}
})
}
}
}



