
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'


export default {
name: 'resetSetting',
directives: {
TransferDom
},
components: {
XInput,
ViewBox,
XButton,
XHeader,
Box,
Divider,
Group,
Cell,
Msg,
CellBox,
PopupHeader,
Popup
},
mounted: function () {
this.currentUser = JSON.parse(window.localStorage.getItem('User'))
this.newNickName = this.currentUser.nickName
},
data () {
return {
msg: 'resetSetting page',
showEdit: false,
newNickName: '',
currentUser: {},
editModel: {
title: '',
currentEdit: 'nickName'
}
}
},
methods: {
nickNameReset () {
this.editModel.title = ''
this.editModel.currentEdit = 'nickName'
this.showEdit = true




},
realNameReset () {
this.editModel.title = ''
this.editModel.currentEdit = 'realName'
this.showEdit = true
},
doReset () {

if (this.editModel.currentEdit === 'realName' && this.currentUser.userStatus === true) {
this.showEdit = false
}
if (this.editModel.currentEdit === 'realName' && this.currentUser.userStatus === false) {
console.log('')
let requestOptions = {
url: this.AppConfig.apiServer + '/user/initUserInfo',
params: {
userid: this.currentUser.userId,
realName: this.currentUser.realName,
identityNo: this.currentUser.identityNo
}
}
this.doRequest(requestOptions)
}

if (this.editModel.currentEdit === 'nickName') {
let requestOptions = {
url: this.AppConfig.apiServer + '/user/updateNickName',
params: {
userid: this.currentUser.userId,
oldNickName: this.currentUser.nickName,
newNickName: this.newNickName
}
}
if (this.newNickName === this.currentUser.nickName) {
this.showEdit = false
} else {
this.doRequest(requestOptions)
}
}
},
doRequest (requestOptions) {
this.doPost(requestOptions).then(result => {
if (result.success) {
this.updateUserInfo()
this.showEdit = false
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'text',
text: result.message,
width: '10em'
})
}
})
},
updateUserInfo () {
let requestOptions = {
url: this.AppConfig.apiServer + '/user/findByUserId?userid=' + this.currentUser.userId
}
this.doGet(requestOptions).then(result => {
if (result.success) {
this.currentUser = result.data
window.localStorage.setItem('User', JSON.stringify(result.data))
}
})
}
}
}



