
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
name: 'SecuritySetting',
components: {
XInput,
ViewBox,
XButton,
XHeader,
Box,
Divider,
Group,
Cell,
CellBox
},
mounted: function () {
console.log(this.currentUser.id)
let userObj = JSON.parse(window.localStorage.getItem('User'))
this.currentUser.nickName = userObj.nickName
},
data () {
return {
msg: 'resetSetting page',
showEdit: false,
nickName: '',
currentUser: {
nickName: ''
},
editModel: {
title: '',
currentEdit: 'nickName'
}
}
},
methods: {
logout () {
let homeObj1 = {
headerStatus: false,
nowView: 'wakuang'
}
this.$store.commit('updateHomeObj', homeObj1)
window.localStorage.clear()
window.sessionStorage.clear()
this.$router.push({'name': 'home'})
}
}
}



