
import XButton from 'vux/src/components/x-button/index.vue'

export default {
props: ['isclID', 'addISC'],
components: {XButton},
mounted: function () {
this.jiaodu = (Math.random() * 1000)
},
data () {
return {
jiaodu: '50',
showStatus: true,
showNumber: false,
isMiss: '',
isShow: ''
}
},
methods: {
clickCoin: function () {
this.$emit('getCoin', this.isclID)
console.log('click img')
this.isMiss = 'miss'
this.isShow = 'show'
this.showNumber = true
var _that = this
setTimeout(function () {
_that.showStatus = false
}, '900')
}
}
}



