
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
Group,
XInput,
XButton,
Box,
Flexbox,
FlexboxItem
},
data () {
return {
telNum: '',
vcode: '',
reget: '',
password: '',
yaoqingma: '',
waiting: false,
counter: 59
}
},
methods: {
doRegister () {
if (this.$refs.input1.valid && this.$refs.input2.valid && this.$refs.input3.valid) {
console.log('do register')
let requestOptions = {
url: this.AppConfig.apiServer + '/user/firstsave',
params: {
phone: this.telNum.replace(/[ ]/g, ''),
password: this.password,
smsCode: this.vcode,
invitationCode: this.yaoqingma
}
}
this.doPost(requestOptions).then(result => {
if (result.success) {
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'text',
text: result.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: ''
})
}
},
login () {
this.$router.push({name: 'login'})
},
nextStep () {
this.$router.push({name: 'home'})
},
getSmsCode () {
if (this.$refs.input1.valid) {
console.log(this.telNum)
console.log(this.telNum.replace(/[ ]/g, ''))
const urlstr = this.AppConfig.apiServer + '/sms/getCodeByPhone/' + this.telNum.replace(/[ ]/g, '')
this.doGet({
url: urlstr
}).then(result => {
if (result.success) {
this.waiting = true
this.timer = setInterval(this.changeBtnText, 1000)
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'warn',
text: ''
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
},
changeBtnText () {
this.reget = '(' + this.counter + ')'
this.counter --
if (this.counter < 0) {
this.waiting = false
this.reget = ''
window.clearInterval(this.timer)
this.counter = 59
}
}
},
computed: {
isInput () {
return this.vcode.length !== 4
}
}
}



