
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import ProgramHeader from './components/ProgramHeader'
export default {
props: {
program: Object,
showTitle: Boolean,
programName: String
},
directives: {
TransferDom
},
components: {
ViewBox,
Popup,
Cell,
ProgramHeader
},
data () {
return {
viewAbout: false,
titleStr: this.programName,
styleObj: {
'z-index': 800
}
}
},
computed: {
title () {
return this.titleStr
}
},
mounted () {
},
methods: {
changeTitle (newStr) {
this.titleStr = newStr

},
closeProgram () {
this.viewAbout = false
this.$emit('close-program')
let that = this
this.$nextTick(() => {
that.$refs.refProgram.closeWindow()
})
},
closeAbout () {
this.viewAbout = false
},
openAbout () {


this.viewAbout = true
},
clickAbout () {
let that = this
this.$nextTick(() => {
that.$refs.refProgram.openAbout()
})
}
}
}



