
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import Coin from './GetCoin'
export default {
mounted: function () {
this.gundong()
this.init()
console.log('load ...')
},
name: 'wakuang',
components: {
Coin,
MarqueeItem,
XTable
},
data () {
return {
chengjiudian: 0,
gonggao: '',
totalPeople: 10000,
coins: [],
paihangList: []
}
},
methods: {
init () {
let user = JSON.parse(window.localStorage.getItem('User'))
this.chengjiudian = user.calculateValue
this.getCoinRequest(user.userId)
this.getPaihangBang()
},
doGetCoin (iscLID) {
console.log('ID: ' + iscLID)
const urlstr = this.AppConfig.apiServer + '/calculate/checkCalculateLog/' + iscLID
this.doGet({
url: urlstr
}).then(result => {
if (result.success) {
console.log('')
} else {
this.$vux.toast.show({
type: 'cancel',
text: result.message
})
}
})
},
mingciCSS: function (item) {
if (item === 1) {
return 'first'
} else if (item === 2) {
return 'second'
} else if (item === 3) {
return 'third'
} else {
return 'other'
}
},
getCoinRequest (userId) {
const urlstr = this.AppConfig.apiServer + '/calculate/getCalculateLog/' + userId
this.doGet({
url: urlstr
}).then(result => {
if (result.success) {
this.coins = result.data
} else {
this.$vux.toast.show({
type: 'cancel',
text: result.message
})
}
})
},
getPaihangBang () {
const urlstr = this.AppConfig.apiServer + '/calculate/getCalculateStatistic100'
this.doGet({
url: urlstr
}).then(result => {
if (result.success) {
this.paihangList = result.data
} else {
this.$vux.toast.show({
type: 'cancel',
text: result.message
})
}
})
}
},
computed: {}
}



