
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
Group,
XInput,
XButton,
Box,
Flexbox,
FlexboxItem
},
data () {
return {
telNum: '',
password: ''
}
},
methods: {
resetPasswd () {
this.$router.push({name: 'resetPasswd'})
},
register () {
console.log('goto register')
this.$router.push({name: 'register'})
},
doLogin () {
if (this.$refs.input1.valid && this.$refs.input2.valid) {
let requestOption = {
url: this.AppConfig.apiServer + '/user/login',
params: {
account: this.telNum.replace(/[ ]/g, ''),
password: this.password
}
}
this.doPost(requestOption).then(result => {
if (result.success) {
console.log(result)
const userObj = result.data
window.localStorage.setItem('User', JSON.stringify(userObj))
this.$router.push({name: 'home'})
window.sessionStorage.setItem('/', 0)
this.$vux.toast.show({
type: 'success',
text: result.message
})
} else {
this.$vux.toast.show({
type: 'text',
text: result.message,
width: '10em'
})
}
})
}
},
nextStep () {
if (this.$refs.input1.valid) {
const urlstr = this.AppConfig.apiServer + '/user/findByAccount?account=administrator'
this.doGet({
url: urlstr
}).then(result => {
if (result.success) {
console.log(result.data)
} else {
console.log('')
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
}
},
computed: {
isDisableLogin () {
return this.telNum.length === 0 || this.password.length === 0
}
}
}



