
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import {WaKuang, My, Activity} from './homeIndex'

export default {
components: {
ViewBox,
Group,
Cell,
XButton,
XHeader,
Tabbar,
TabbarItem,
'wakuang': WaKuang,
'my': My,
'activity': Activity
},
data () {
return {
msg: 'Hello World!'
}
},
computed: {
currentView: {
get: function () {
return this.$store.state.vux.homeObj.homeView
},
set: function () {
}
},
showHeader: {
get: function () {
return this.$store.state.vux.homeObj.headerStatus
},
set: function () {
}
}
},
methods: {
changeView (view) {
let homeObj1 = {
headerStatus: false,
nowView: 'wakuang'
}
if (view === 'wakuang') {
homeObj1.headerStatus = false
} else {
homeObj1.headerStatus = true
}
this.$store.commit('updateHeaderStatus', homeObj1)
homeObj1.nowView = view
this.$store.commit('updateHomeView', homeObj1)
},
back () {

this.$router.back(-1)
},
gotoHome () {
this.$router.push({name: 'home'})

}
}
}



