
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'

import ProgramButton from '../components/ProgramHeader'
export default {
props: {
showTitle: Boolean
},
directives: {
TransferDom
},
components: {
ProgramButton,
Scroller,
ViewBox,
XButton,
XHeader,
PopupHeader,
Popup,
Group,
LoadMore
},
data () {
return {
showPop: false,
currentPage: 'home',
styleObj: {
'z-index': 788,
'height': 'calc(100% - 46px)',
'top': '46px'
},
pullUpConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
pullDownConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
publicMailList1: [
{
sender: 'xxx@163.com',
toTime: '2020',
content: '1xxxxx1111111111'
}
],
publicMailList: [
{
sender: 'xxx@163.com',
toTime: '2020',
content: 'xxxx1111111111'
},
{
sender: 'xxx@163.com',
toTime: '2020',
content: '2111111'
}
]
}
},
mounted () {
this.$nextTick(() => {
this.$refs.demo1.reset({top: 0})
})
},
methods: {
vieMail () {
this.showPop = true
this.currentPage = 'mailList'
this.changeTitle('')




},
changeTitle (title) {
this.$emit('change-title', title)
},
writeMail () {
this.$router.push({name: 'wirteMail'})
},
closeWindow () {
this.changeTitle('')
this.currentPage = 'home'
this.showPop = false
},
backHome () {
this.changeTitle('')
this.currentPage = 'home'
this.showPop = false
},
openAbout () {
this.changeTitle('')
this.showPop = false
this.currentPage = 'about'
this.$emit('close-about')
},
pullUpLoad () {
setTimeout(() => {
this.publicMailList = this.publicMailList.concat(this.publicMailList)
setTimeout(() => {
this.$refs.demo1.donePullup()
}, 100)
}, 1000)
},
pullDownLoad () {
setTimeout(() => {
this.publicMailList = this.publicMailList1.concat(this.publicMailList)
setTimeout(() => {
this.$refs.demo1.donePulldown()
}, 100)
}, 1000)
}
}
}



